<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Reviews List</h1>
      <div class="chartWrap">
        <a class="iconfont cart" @click="$router.push('cart')"> &#xe669; </a>
      </div>
    </section>
    <div class="reviewsOrder">
      <ul>
        <li v-for="(item, index) in orderDetail" :key="index">
          <div class="starWrap">
            <div class="leftWrap">
              <img :src="item.img0" />
            </div>
            <div class="rightWrap">
                <div class="star">
                    <van-rate
                        v-model="item.points"
                        :size="15"
                        color="#f44"
                        void-icon="star"
                        void-color="#eee"
                    /><span>{{ item.points }}</span>
                </div>
                <p> Tap on star to rate</p>
            </div>
          </div>
          <div class="comment">
           
            <div class="labelWrap">
              <div
                :class="['setmeal', label.checked ? 'red' : '']"
                v-for="(label, _i) in item.labelList"
                @click="selectLabel(label)"
                :key="_i"
              >
                {{ label.name }}
              </div>
            </div>
            <div class="leaving">
                <p>Review<i>*</i></p>
                <textarea  v-model="item.review" placeholder="min 50 charactes"></textarea>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <van-button type="danger" @click="submitReview">Submit</van-button>
  </div>
</template>

<script>
import { writeCommentProInfo, commentSubmit } from "@/api/product";
export default {
  name: "reviewsOrder",
  data() {
    return {
      value: "5",
      orderNo: this.$route.query.orderNo,
      orderDetail: []
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      writeCommentProInfo({
        orderNo: this.orderNo
      }).then(res => {
        this.orderDetail = res.data.result;
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.bus.$emit('goBack');;
    },
    submitReview() {
        let data=[];
        this.orderDetail.forEach(e=>{
            if(e.review&&e.points){

                data.push({
                    spuCode:e.spuCode,
                    orderNo:this.orderNo,
                    comment:e.review,
                    points:e.points,
                    labelIdList:e.labelList.filter(t=>{return t.checked;}).map(t=>{return t.id;}).join(',')
                });
            }
        });
      commentSubmit(data).then(res => {});
    },
    selectLabel(label) {
        console.log(1)
      this.$set(label, "checked",! label.checked);
    }
  }
};
</script>

<style lang="scss" scoped>
.reviewsOrder {
  ul {
    background-color: #f6f6f6;
    display: inline-block;
    width: 100%;
  }
  li {
    border-top: 10px #f6f6f6 solid;
    padding: 0 4%;
    background-color: white;
    padding-bottom: 10px;
  }
}

.starWrap {
  width: 100%;
  border-bottom: 1px solid #f6f6f6;
}
.starWrap {
  height: 90px;
  display: flex;
  align-items: center;
}
.leftWrap img {
  width: 30px;
  height: 60px;
}
.leftWrap {
  background-color: #f2f2f2;
  width: 18%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80%;
}
.rightWrap {
  width: 82%;
  height: 80%;
  padding-left: 20px;
  span {
    width: 218px;
    font-size: 14px;
    display: block;
    color: #111111;
  }
  p {
    font-size: 13px;
    color: #2b87d5;
    padding-top: 12px;
    padding-left: 10px;
  }
}
.scoreWrap {
  display: flex;
  align-items: center;
  padding: 10px 0 5px 10px;
}
.scoreWrap span {
  color: #e33946;
  padding-left: 10px;
}
.tap {
  color: #2b87d5;
  padding-left: 20px;
  font-size: 11px;
}
.comment {
  width: 100%;
  padding: 0 0px 10px 0px;
  height: 127px;
}
.star {
  display: -webkit-box;
  position: relative;
  padding-top: 10px;
  span {
    color: #e33946;
    font-size: 15px;
    padding-right: 5px;
    padding-left: 12px;
    display: flex;
    align-items: center;
  }
  .van-rate {
    padding-top: 2.5px;
  }
}
.labelWrap {
  width: 100%;
  display: table;
}
.setmeal {
  width: 91px;
  height: 27px;
  font-size: 13px;
  line-height: 26px;
  border: #666666 0.5px solid;
  border-radius: 2px;
  text-align: center;
  color: #666666;
  float: left;
  margin-top: 15px;
  margin-right: 20px;
}
.red {
  border: #e33946 1px solid;
  color: #e33946;
}
.leaving {
  width: 100%;
  p{
      font-size: 14px;
      color: #333333;
      padding-top: 17px;
      padding-bottom: 12px;
      i{
          color: #e33946;
      }
  }
  textarea{
      width: 100%;
      height: 145px;
      background-color: #f6f6f6;
      padding: 10px;
      margin-bottom: 20px;
  }
}
.van-rate__icon{
    font-size: 20px !important;
}
.van-button--danger{
    width: 92%;
    margin-left: 4%;
}
</style>



